.card{
    background-color: #95dada;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    padding: 10px;
    font-family: 'Macondo';
    cursor: pointer;
    transition: transform .5s ease-in-out ;
}
.card:hover{
    background-color: orange;
    transform: scale(.9);
}